<script>
import { getPersonalInfo, getPersonalBalance,getUserLevel,getUserCountT, wxApi,getSysValue } from "@/api";
export default {
  data() {
    return {
      realName: "",
      headPic: "",
      userCode: "",
      empType: 0,
      encourage: 0,
      voucher: 0,
      balance: 0,
      userLevel: 0,
      userCountT: 0,
      userLevelName: "",
      myTeamShow: 0,
      list: [ 
        {
          text: "收货地址",
          icon: require("@/assets/img/icon/userIcon10.png"),
          router: { name: "myAddress" },
          empType: -1,
          userLevel: 0
        }, 
        {
          text: "扫一扫",
          icon: require("@/assets/img/icon/userIcon19.png"),
          router: { name: "" },
          empType: -1,
          userLevel: 0
        },
        {
          text: "分享",
          icon: require("@/assets/img/icon/userIcon22.png"),
          router: { name: "recommend" },
          empType: 1,
          userLevel: 0
        },
        {
          text: "钱包",
          icon: require("@/assets/img/icon/userIcon12.png"),
          router: { name: "machinery" },
          empType: 1,
          userLevel: 0
        },
        {
          text: "团队",
          icon: require("@/assets/img/icon/userIcon4.png"),
          router: { name: "my" },
          empType: 1,
          userLevel: 0
        },
        {
          text: "OTC",
          icon: require("@/assets/img/icon/userIcon4.png"),
          router: { name: "otc" },
          empType: 1,
          userLevel: 0
        },
        {
          text: "退出登录",
          icon: require("@/assets/img/icon/userIcon4.png"),
          event: '_logout',
          empType: 1,
          userLevel: 0
        },
        {
          text: "我的团队",
          icon: require("@/assets/img/icon/userIcon4.png"),
          router: { name: "myTeam" },
          empType: 1,
          userLevel: 1
        }, 
        //,
        // {
        //   text: "收积分",
        //   icon: require("@/assets/img/icon/userIcon22.png"),
        //   router: {
        //     name: "paymentCode",
        //     query: {
        //       type: "2"
        //     }
        //   },
        //   empType: -1
        // },
        {
          text: "优惠券",
          icon: require("@/assets/img/icon/userIcon23.png"),
          router: { name: "couponList" },
          empType: -1
        },
        // {
        //   text: "商户中心",
        //   icon: require("@/assets/img/icon/userIcon25.png"),
        //   router: { name: "merchantsCenter" },
        //   empType: 2
        // },
        // {
        //   icon: require("@/assets/img/icon/userIcon21.png"),
        //   text: "商户收款码",
        //   router: {
        //     name: "paymentCode",
        //     query: {
        //       type: "1"
        //     }
        //   },
        //   empType: 1
        // },
        // {
        //   icon: require("@/assets/img/icon/userIcon24.png"),
        //   text: "核销订单",
        //   router: { name: "closeOrderList" },
        //   empType: 1
        // }
      ],
      accountList: [],
    };
  },
  methods: {
    toApply() {
      if (this.userLevel < 3) {
        this.$router.push({
          name: "agentRegistration",
        });
      }
    },
    tap(item) {
      this.$router.push({
        name: "bonus",
        query: { code: item.ptCode, name: item.ptName },
      });
    },
    to(item) {
      let router = item.router;
      let event = item.event;
      if(router){
        if (router.name) {
          this.$router.push(router);
        } else {
          this.scanQRCode();
        }
      }else if(event){
        this[event]();
      }
      
    },
    scanQRCode() {
      let that = this;
      wxApi()
        .then(res => {
          this.$wx.config({
            debug: false,
            appId: res.data.appId,
            timestamp: res.data.timestamp,
            nonceStr: res.data.nonceStr,
            signature: res.data.signature,
            jsApiList: ["scanQRCode"],
          });
          this.$wx.ready(function() {
            that.$wx.scanQRCode({
              needResult: 0, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
              scanType: ["qrCode", "barCode"], // 可以指定扫二维码还是一维码，默认二者都有
              success: function(res) {
                var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
              },
            });
          });
        })
        .catch(err => {
        });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      getPersonalInfo().then(res => {
        loading.clear();
        if (res.code == 0) {
          this.realName = res.data.userName;
          this.headPic = res.data.userHeader;
          this.empType = parseInt(res.data.empType);
          this.userLevel = parseInt(res.data.userLevel);
          sessionStorage.setItem("userCode", res.data.userCode);

          getUserLevel({
              userLevel: this.userLevel, 
          }).then(res => {
              loading.clear();
              if (res.code == 0) {
                this.userLevelName = res.data; 
              } else {
                this.$toast.fail(res.msg);
              }
          });

          getUserCountT().then(res => {
              loading.clear();
              if (res.code == 0) {
                this.userCountT = res.data; 
              } else {
                this.$toast.fail(res.msg);
              }
          });


        } else {
          this.$toast.fail(res.msg);
        }
      });
      getPersonalBalance().then(res => {
        if (res.code == 0) {
          this.accountList = res.data;
        } else {
          this.$toast.fail(res.msg);
        }
      }); 
      getSysValue({key: 'fil_myteam_show'}).then(res => {
        if(res.code== 0)
        {
            this.myTeamShow = res.data.keyv;
        }
         
      })
    },
    _logout(){
      sessionStorage.removeItem('token');
      localStorage.removeItem('token');
      this.$toast.success('退出登录成功！');
      this.$router.push('/myLogin');
    }
  },
  mounted() {
    document.title = "我的";
    this.loadData();
  },
};
</script>
